import React from "react";
import api from './coingecko-api';
import { Container, Row, Col } from "react-grid-system";


export default function Home() {

    const [list, setList] = React.useState();
    const currencies = ["AED","ARS","AUD","BCH","BDT","BHD","BITS","BMD","BNB","BRL","BTC","CAD","CHF","CLP","CNY","CZK","DKK","DOT","EOS","ETH","EUR","GBP","HKD","HUF","IDR","ILS","INR","JPY","KRW","KWD","LINK","LKR","LTC","MMK","MXN","MYR","NGN","NOK","NZD","PHP","PKR","PLN","RUB","SAR","SATS","SEK","SGD","THB","TRY","TWD","UAH","USD","VEF","VND","XAG","XAU","XDR","XLM","XRP","YFI","ZAR"];

    React.useEffect(() => {
      api
        .get("/coins/list")
        .then((response) => setList(response.data))
        .catch((err) => {
          console.error("ops! ocorreu um erro" + err);
        });
    }, []);

    const [todos, setTodos] = React.useState([
        {
          token: "Bitcoin",
          currency: 'USD'
        }
      ]);
      const addTodo = (token, currency) => {
        const newTodos = [...todos, { token, currency }];
        setTodos(newTodos);
      };

      const removeTodo = index => {
        const newTodos = [...todos];
        newTodos.splice(index, 1);
        setTodos(newTodos);
      };

    return(
        <Container>
            <Row>
                <Col>
                    <h1>Coingecko Panel</h1>
                    <select name="token" value="bitcoin">
                    {list?.map(function(token, i){
                        return <option value={token.id}>{token.name}</option>
                    })}
                    </select>   
                    <select name="currency" value="USD">
                    {currencies?.map(function(currency, i){
                        return <option value={currency}>{currency}</option>
                    })}
                    </select>
                    <button>Adicionar</button>
                </Col>
                <Col>
                {todos.map(function(todo, i){
                    <p>{todo.token} ({todo.currency})</p>
                })}
                </Col>
            </Row>
        </Container>
    );
}